import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';

import {useRouter} from '@unexp/router';

import {
	View, ViewWidth, ScreenSpinner, useAdaptivity, AdaptivityProvider,
	AppRoot, ConfigProvider, SplitLayout, SplitCol, Root,
} from '@vkontakte/vkui';

import '@vkontakte/vkui/dist/vkui.css';
import './panels/def.css';

import {
	Panel, PanelHeader, Placeholder, Header,
	Button, Group, Cell, Div,
	Avatar, Tabbar, TabbarItem, ModalRoot, ModalCard, Link
} from '@vkontakte/vkui';

import {
	Icon28MarketAddBadgeOutline, Icon16ErrorCircleFill, Icon28Profile,
	Icon28MarketOutline, Icon28FavoriteOutline, Icon24ClockOutline, Icon16Verified, Icon28HomeOutline,
	Icon28More, Icon28ClockCircleFillGray
} from '@vkontakte/icons';

// СТРАНИЦЫ ПРИЛОЖЕНИЯ
import Home from './panels/Home';

import {useStructure} from '@unexp/router'

const App22 = () => {
	const { viewWidth } = useAdaptivity();

	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<ConfigProvider isWebView={true}>
			<AdaptivityProvider>
				<AppRoot>
					<SplitLayout header={<PanelHeader separator={true} />}>
						<SplitCol spaced={viewWidth > ViewWidth.MOBILE}>

							<View activePanel={activePanel} popout={popout}>
								<Home id='home' fetchedUser={fetchedUser} go={go} />

								<Panel id='homeqq' fetchedUser={fetchedUser} go={go}>
									<PanelHeader>Главная</PanelHeader>


									{fetchedUser && fetchedUser.id === 627581492 &&
										<Group>
											<Cell
												disabled
												before={fetchedUser.photo_200 ? <React.Fragment> <Avatar src={fetchedUser.photo_200}> <Icon16Verified style={{ "float": 'right' }} /> </Avatar> </React.Fragment> : null}
												description="Pro"
											>
												Здравствуйте, {`${fetchedUser.first_name} ${fetchedUser.last_name}`}!
									</Cell>
										</Group>}

									{fetchedUser && fetchedUser.id !== 627581492 &&
										<Group>
											<Cell
												disabled
												before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}> </Avatar> : null}
												description={fetchedUser.id}
											>
												Здравствуйте, {`${fetchedUser.first_name} ${fetchedUser.last_name}`}!
									</Cell>
										</Group>}

									{!fetchedUser &&
										<Placeholder
											icon={<Icon24ErrorCircleOutline width={56} height={56} />}
											header="Не удалось получить данные"
											action={<Button size="m">Загрузить профиль</Button>}
										>
											Нажмите ниже на кнопку, чтобы загрузить профиль.
								</Placeholder>}
								</Panel>

								<Panel id='pay' go={go}>
									<PanelHeader>Покупка</PanelHeader>

									<Placeholder
										icon={<Icon24ClockOutline width={56} height={56} />}
										header="Упс!"
									>
										Этот раздел временно не доступен.
								</Placeholder>
								</Panel>

								<Panel id='rating' go={go}>
									<PanelHeader>Рейтинг</PanelHeader>

									<Placeholder
										icon={<Icon24ClockOutline width={56} height={56} />}
										header="Упс!"
									>
										Этот раздел временно не доступен.
								</Placeholder>
								</Panel>

							</View>

							<Tabbar>
								<TabbarItem style={activePanel === 'home'} selected={activePanel === 'home'} onClick={go} data-to="home" text="Мой профиль">
									<Icon28Profile />
								</TabbarItem>

								<TabbarItem selected={activePanel === 'pay'} onClick={go} data-to="pay" text="Покупка">
									<Icon28MarketOutline />
								</TabbarItem>

								<TabbarItem selected={activePanel === 'rating'} onClick={go} data-to="rating" text="Рейтинг">
									<Icon28FavoriteOutline />
								</TabbarItem>
							</Tabbar>

						</SplitCol>
					</SplitLayout>
				</AppRoot>
			</AdaptivityProvider>
		</ConfigProvider>
	);
}

export function App() {
	let structure = useStructure({ view: 'home', panel: 'main' });
	let {modal, panel} = useStructure({ view: 'home', panel: 'main' });
	let {push, back} = useRouter();
	let {replace} = useRouter();
	const { viewWidth } = useAdaptivity();
	const keyacc = '';

	return (
		<ConfigProvider isWebView={true}>
			<AdaptivityProvider>
				<AppRoot>
					<SplitLayout header={<PanelHeader separator={true} />}>
        				<SplitCol spaced={viewWidth > ViewWidth.MOBILE}>
							<Root activeView={structure.view}>

								<View id='home' activePanel={structure.panel} modal={
									<ModalRoot activeModal={modal} onClose={back}>
										<ModalCard id='info' onClose={back}>
											<React.Fragment>
												<h1>Код ошибки - Error A1.</h1> <br /> Решение проблемы: Выйдайте доступ приложению, чтобы мы могли работать в полноценном режиме.
												<Button mode="secondary" disabled style={{"border-radius": '3px', "bottom": '-8px', "align": 'right'}}>Предоставить доступ к аккаунту</Button>
											</React.Fragment>
											</ModalCard>
									</ModalRoot>
								}>
									<Panel id='main'>
										<PanelHeader>Главная</PanelHeader>

										{keyacc === '' && 
											<Placeholder 
											icon={<Icon16ErrorCircleFill fill="red" width={56} height={56} />}
											header="Error A1"
											action={<Link onClick={() => push({ modal: 'info' })}>Узнать подробнее</Link>}
											>
											
											</Placeholder>
										}

										<Tabbar>
											<TabbarItem selected={structure.view === 'home'} text="Главная" onClick={() => replace({ view: 'home', panel: 'main' })}> <Icon28HomeOutline /> </TabbarItem>
											<TabbarItem selected={structure.view === 'more'} text="Ещё" onClick={() => replace({ view: 'more', panel: 'main' })}> <Icon28More /> </TabbarItem>
										</Tabbar>
									</Panel>
									
									
								</View>

								<View id='more' activePanel={structure.panel}>
									<Panel id='main'>
										<PanelHeader>Ещё</PanelHeader>

										<Group>Интересный факт, на эту вкладку зашло 500тыс. человек.</Group>

										<Tabbar>
											<TabbarItem selected={structure.view === 'home'} text="Главная" onClick={() => replace({ view: 'home', panel: 'main' })}> <Icon28HomeOutline /> </TabbarItem>
											<TabbarItem selected={structure.view === 'more'} text="Ещё" onClick={() => replace({ view: 'more', panel: 'main' })}> <Icon28More /> </TabbarItem>
										</Tabbar>
									</Panel>
								</View>

							</Root>
						</SplitCol>
					</SplitLayout>
				</AppRoot>
			</AdaptivityProvider>
		</ConfigProvider>
	)			
}

export default App;
